import React from 'react';
import './blog.scss';
import BlogHeader from './module6/6';
import BlogCategories from './module10/10';
import BlogMember from './module7/7';
import BlogInstaGram from './module11/11';
import BlogProduct from './module8/8';
import BlogWomen from './module9/9';

class Blog extends React.Component {
    render() {
        return (
            <div>
                <div className="bg-header">
                    <h3 className="blog-header">BLOG</h3>
                    <div><span><i className="fa fa-home"></i>Home</span><span className="range-blog">/</span><span>New</span></div>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-9">
                            <BlogHeader />
                            <BlogMember />
                            <BlogProduct />
                            <BlogWomen />
                        </div>
                        <div className="col-md-3 size-row">
                           <BlogCategories />
                           <BlogInstaGram />
                        </div>
                    </div>
                </div>
            </div>    
        );
    } 
 }
 export default Blog;