import React from 'react';
import './29.scss';
import { BrowserRouter, Route, Link } from "react-router-dom";
import Home from '../Home/Home';
import AboutUs from '../AboutUs/Aboutus';
import Shop from '../Shop/Shop';
import Blog from '../Blog/Blog';
import ContactUs from '../Contact US/ContactUs';
import OurShop from '../OurShop/OurShop';
import OurService from '../OurService/Ourservice';
import Login from '../Login/Login';

class Menu extends React.Component {
  
    render() {
    return (
    <div>
    <div className="container-build">
        <div className="top-header">
            <div className="row header-size">
                <div className="col-md-4 col-sm-6 col-xs-12">
                    <ul>
                        <li>
                        <span className="ourphone">OUR PHONE NUMBER: +84(123) 456.789</span>
                        </li>
                    </ul>
                </div>
                <div className="col-md-8 col-sm-6 col-xs-12">
                    <ul className="item-info">
                    <li className="item">
                        <a href="#"><i className="fa fa-user"></i> My Account</a>
                    </li>
                    <li className="item">
                        <a href="#"><i className="fa fa-home"></i>Our Service</a>
                    </li>
                    <li className="item">
                        <a href="#"><i className="fa fa-location-arrow"></i>Shop Location</a>
                    </li>
                    <li className="item">
                        <a href="#"><i className="fa fa-users"></i>Contact Us</a>
                    </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <BrowserRouter>
    <nav className="navbar navbar-default nav-menu-header" role="navigation">
            <div className="container-fluid fluid-menu">
                
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
                        <span className="sr-only">Toggle navigation</span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <a className="navbar-brand link-home" href="#">
                    <img className="img-responsive img-logo-menu" alt="menu1" src={require('./images/logo1.png')}/>
                    </a>
                </div>
                <div className="collapse navbar-collapse navbar-ex1-collapse">
                    <ul className="nav navbar-nav right-menu-float">
                        <li><Link to="/">Home</Link></li>
                        <li className="link-menuhide"><Link to="/nhomC-cdptw2/">Home</Link></li>
                        <li><Link to="/about">About Us</Link></li>
                        <li><Link to="/shop">Shop</Link></li>
                        <li><Link to="/blog">Blog</Link></li>
                        <li className="dropdown">
                            <a href="#" className="dropdown-toggle" data-toggle="dropdown">Pages <b className="caret"></b></a>
                            <ul className="dropdown-menu dropdown-menu-pages">
                                <li><Link to="contact">Contact Us</Link></li>
                                <li><Link to="/ourshop">Our Shop</Link></li>
                                <li><Link to="/ourservice">Our Service</Link></li>
                            </ul>
                        </li>
                    </ul>
                    <ul className="nav navbar-nav navbar-right menu-icon">
                        <li><Link to="/login">Login / Register</Link></li>
                        <li className="menu-icon-right">
                            <form action="" className="form-search">
                                <input type="search" className="search" placeholder="Search..." ></input>
                                <i className="fa fa-search search-button"></i>
                            </form>
                        </li>
                        <li className="item-cart">
                            <a href="#" id="opennav"><i className="fa fa-shopping-cart cart-button"></i>Cart</a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        <div className="Route-main"></div>
        <Route exact path="/" component={Home} />
        <Route exact path="/nhomC-cdptw2/" component={Home} />
        <Route path="/about" component={AboutUs} />
        <Route path="/shop" component={Shop} />
        <Route path="/blog" component={Blog} />
        <Route path="/contact" component={ContactUs} />
        <Route path="/ourshop" component={OurShop} />
        <Route path="/ourservice" component={OurService} />
        <Route path="/login" component={Login} />
        </BrowserRouter>
        <div id="mySidenav" className="product-cart">
            <div className="btn-close-cart">
                <h3 className="title-cart">Shopping Now</h3>
                <a href="#" className="closebtn">CLOSE</a>
            </div>
        
            <div className="product-item">
                <ul>
                    <li>
                        <a href="#">
                            <img className="img-responsive img-logo-menu" alt="cart1" src={require('./images/giay3.jpg')}/>
                            <span className="product-title">Ymolestie Condim</span>
                        </a>
                        <span className="price">£750.00</span>
                    </li>
                    <li>
                        <a href="#">
                        <img className="img-responsive img-logo-menu" alt="cart2" src={require('./images/giay2.jpg')}/>
                            <span className="product-title">Ynisi Condimentum</span>
                        </a>
                        <span className="price">£650.00</span>
                    </li>
                </ul>
            </div>
            <div className="total-cart">
                <div className="total">
                    <h4><b>SUBTOTAL:</b></h4>
                    <span>
                        <h4><b>£1,400.00</b></h4>
                    </span>
                </div>
                <button className="btn-custom">VIEW CART</button>
                <button className="btn-custom">CHECKOUT</button>
            </div>
        </div>
        <div className="close-cart"></div>
     </div>
    );
   } 
  }
  
  
  export default Menu;